<template>
    <main id="art-cycle" class="p-m-2">
        <div class="p-grid">
            <div class="p-col-12 p-md-6">
                <patient-search />
            </div>
            <div class="p-col-12 p-md-6">
                <partner-search />
            </div>
        </div>

        <div class="p-grid">
            <div class="p-col-12">
                <cycle-form />
            </div>
        </div>

        <div style="height: calc(100vh - 575px)">
            <cycles-summary />
        </div>

        <div class="p-grid p-mt-1">
            <div class="p-col-12 p-md-6">
                <lab-test type="female" />
            </div>
            <div class="p-col-12 p-md-6">
                <lab-test type="male" />
            </div>
        </div>

        <p-button type="button" label="Save" class="p-mr-2"></p-button>
    </main>
</template>

<script>
import { provide, ref, watch } from "vue";
import { emitter } from "@/event-bus";

import Service from "@/service/art-cycle";

import PatientSearch from "@/components/art-cycle/PatientSearch.vue";
import PartnerSearch from "@/components/art-cycle/PartnerSearch.vue";
import CyclesSummary from "@/components/art-cycle/CyclesSummary.vue";
import CycleForm from "@/components/art-cycle/CycleForm.vue";
import LabTest from "@/components/art-cycle/LabTest.vue";

export default {
    components: {
        PatientSearch,
        PartnerSearch,
        CyclesSummary,
        CycleForm,
        LabTest,
    },
    setup() {
        const dropdownOpts = ref([
            { label: "Opt 1", value: "opt-1" },
            { label: "Opt 2", value: "opt-2" },
            { label: "Opt 3", value: "opt-3" },
        ]);

        const cyclesSummary = ref([]);
        const patient = ref();
        const partner = ref();
        const cycleTypes = ref();

        provide("cycles", cyclesSummary);
        provide("patient", patient);
        provide("partner", partner);
        provide("cycleTypes", cycleTypes);

        const updateCycle = (data) => {
            let index = cyclesSummary.value.findIndex(
                (item) => item.cycleId === data.cycleId
            );
            Object.assign(cyclesSummary.value[index], data);
        };

        const getCycles = ({ demographicNo }) => {
            Service.getCycleSummaryDetail(demographicNo).then((resp) => {
                let {
                    petientDetails: _patientDetails,
                    partnerDetails: _partnerDetails,
                    cyclesSummery,
                    cycleTypes: _cycleTypes,
                } = resp.data.data;

                cyclesSummary.value = cyclesSummery;
                patient.value = _patientDetails;
                partner.value = _partnerDetails;
                cycleTypes.value = _cycleTypes;
            });
        };

        emitter.on("cycle-table:new-cycle", (data) => {
            cyclesSummary.value.push(data);
        });

        emitter.on("cycle-form:save-cycle", updateCycle);
        emitter.on("patient-search:patient-selected", getCycles);

        return {
            cyclesSummary,
            dropdownOpts,
        };
    },
};
</script>

<style>
#art-cycle .p-card .p-card-content {
    padding: 0;
}
</style>