<template>
    <p-card>
        <template #title> {{ title }} </template>
        <template #content>
            <div class="p-fluid p-formgrid p-grid p-input-filled">
                <div class="p-field p-col-12 p-md-6">
                    <p-auto-complete
                        v-model="demographic"
                        :suggestions="fillteredDemographic"
                        :disabled="disabled"
                        @complete="searchDemographic($event)"
                        @item-select="onDemographicSelect($event)"
                        field="label"
                        placeholder="Type to search"
                    />
                </div>
                <div class="p-field p-col-12 p-md-4">
                    <!-- <p-input-text id="firstname6" type="text" /> -->
                </div>
                <div class="p-field p-col-12 p-md-2">
                    <!-- <span class="p-buttonset">
                        <p-button
                            type="button"
                            icon="pi pi-search"
                            class="p-mr-2"
                        ></p-button>
                        <p-button
                            type="button"
                            icon="pi pi-times"
                            class="p-button-danger"
                        ></p-button>
                    </span> -->
                </div>
                <!-- <div class="p-field p-col-12 p-md-2">
                    <label for="city">Age</label>
                    <p-input-text id="city" type="text" />
                </div>

                <div class="p-field p-col-12 p-md-2">
                    <label for="zip">File</label>
                    <p-input-text id="zip" type="text" />
