            </div>
        </template>
    </p-card>
</template>

<script>
import { inject, ref, watch } from "vue";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import Service from "@/service/art-cycle.js";

export default {
    setup() {
        const title = ref("Male");
        const demographic = ref();
        const fillteredDemographic = ref();
        const partner = inject("partner");
        const disabled = ref(false);

        const searchDemographic = (event) => {
            Service.searchDemographic({
                term: event.query.trim(),
                sex: "M".toLowerCase(),
            })
                .then((resp) => {
                    fillteredDemographic.value = resp.data;
                })
                .catch((err) => {
                    emitter.emit(EVENT_STRINGS.LOGOUT);
                });
        };

        const onDemographicSelect = ({ value }) => {
            emitter.emit("partner-search:partner-selected", value);
        };

        watch(partner, (partner) => {
            demographic.value = {
                ...partner,
                label: `${partner.id} - ${partner.firstName} ${partner.lastName}`,
            };
            disabled.value = true;
        });

        return {
            title,
            demographic,
            disabled,
            fillteredDemographic,
            searchDemographic,
            onDemographicSelect,
        };
    },
};
</script>